.modal {
        font-weight: 100;
        font-size: 11px;
        letter-spacing: 0.1em;
        color: rgb(52, 51, 51);
        text-shadow: 2px 2px 6px rgba(107, 110, 110, 0.5);
        width: 168px;
        padding: 16px;
        border-radius: 8px;
        box-shadow: 0px 0px 15px rgba(199, 203, 203, 0.5);
        display: none;
        position: fixed;
        top: 10%;
        left: 54%;
        transform: translate(-50%, -50%);
        background-color: white;
        border: 1px solid #ccc;
        z-index: 9999;
        padding: 20px;

        @media screen and (max-width: 360px) {
              top: 60%;
              }
      }
      
      .modal.active {
        display: block;
      }

      .close-modal {
        margin: 0;
      }
      
